/* Slideshow stuff */
/*
@import "react-slideshow-image/dist/styles.css";

*/
.each-slide > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.each-slide img {
  width:100%;
  height:inherit;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


/* Custom styles */

.content-table {
  text-align: center;
  width: 100%;
}

div .page-content {
  width: 60%;
  padding-left: 20%;
  text-align: center;
}

.bg-side {
  width: 20%;
  background-color: #efefef;
}

.centered {
  -ms-flex-pack: center;
      justify-content: center;
}

/* Dashed border */
hr.line-dashed {
  border: 3px dashed #bbb;
}

/* Dotted border */
hr.dotted {
  border: 3px dotted #bbb;
}

/* Solid border */
hr.line-solid {
  border: 3px solid #bbb;
}

/* Rounded border */
hr.line-rounded {
  border: 8px solid #bbb;
  border-radius: 5px;
} 

/* The fancy button */
.btn-styled-cqr {
	background-color: #800080;;
	border: 2.5rem;
	border-radius: 8px;
	border-color: none;
	color: white;
	padding: 12px 30px;
	cursor: pointer;
	font-size: 20px;
	font-weight: bold;
}

.btn-styled-cqr:hover {
	background-color: #550055;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
	-webkit-transition: 0.25s;
	-o-transition: 0.25s;
	transition: 0.25s;
}


