nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

nav .navbarItem {
    text-decoration: none;
    font-weight: 500;
    color: black;
    font-size: 1.25em;
    display: inline-block;
    letter-spacing: normal;
    float: none;
    position: relative;
    line-height: 4em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    border-bottom: solid 0.25em white;
}

nav a:hover {
    background-color: rgba(232, 231, 231, 0.5);
    border-bottom: transparent 0.25em rgba(232, 231, 231, 0.5);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

nav .active {
    border-bottom: solid 0.25em #ffd54f;
    background-color: rgba(232, 231, 231, 0.5);
}


